<template>
<div id="list_statistics">
    <StatisticsPlayerList :playerList="playerList" :total="recordsNotScreened.length"></StatisticsPlayerList>
    <StatisticsTeamList :teamList="teamList" :total="recordsNotScreened.length"></StatisticsTeamList>
    <StatisticsCarList :carList="carList" :total="recordsNotScreened.length"></StatisticsCarList>
    <StatisticsPetList :petList="petList" :total="recordsNotScreened.length"></StatisticsPetList>
    <StatisticsEcuList :ecuList="ecuList" :total="recordsNotScreened.length"></StatisticsEcuList>
</div>
</template>

<script>
import StatisticsPlayerList from "@/components/StatisticsPlayerList";
import StatisticsTeamList from "@/components/StatisticsTeamList";
import StatisticsCarList from "@/components/StatisticsCarList";
import StatisticsPetList from "@/components/StatisticsPetList";
import StatisticsEcuList from "@/components/StatisticsEcuList";
export default {
    name: "ListStatistics",
    components: {
        StatisticsPlayerList,
        StatisticsTeamList,
        StatisticsCarList,
        StatisticsPetList,
        StatisticsEcuList
    },
    mounted(){
        this.initRecords();
    },
    data() {
        return {
            recordsNotScreened:[],
            playerList:[],
            teamList:[],
            carList:[],
            petList:[],
            ecuList:[],
        }
    },
    methods: {

        //初始化数据
        initRecords: function(){
            let that = this;
            this.$axios({
                method:'get',
                url:'./a_pet_ecu_records.json'
            }).then((response) =>{
                console.log(response)       //请求成功返回的数据
                that.recordsNotScreened = response.data;
                that.initPlayerList();
                that.initTeamList();
                that.initCarList();
                that.initPetList();
                that.initEcuList();
            }).catch((error) => {
                console.log(error)       //请求失败返回的数据
            });
        },

        //初始化选手数据
        initPlayerList: function(){
            let players = [];
            this.recordsNotScreened.forEach((record,i)=>{
                let index = -1;
                let isExists = players.some((player,j)=>{
                    if(record.player.id==player.player.id){
                        index = j;
                        return true;
                    }
                })
                if(!isExists){
                    players.push({
                        player: record.player,
                        count: 1
                    })
                }else{
                    players[index].count++;
                }
            })
            this.playerList = players.sort(function compareFunction(p1, p2) {
                return p2.count - p1.count;
            });
        },

        //初始化车队数据
        initTeamList: function(){
            let teams = [];
            this.recordsNotScreened.forEach((record,i)=>{
                if(record.player.team){
                    let index = -1;
                    let isExists = teams.some((team,j)=>{
                        if(record.player.team.id==team.team.id){
                            index = j;
                            return true;
                        }
                    })
                    if(!isExists){
                        teams.push({
                            team: record.player.team,
                            count: 1
                        })
                    }else{
                        teams[index].count++;
                    }
                }
            })
            this.teamList = teams.sort(function compareFunction(p1, p2) {
                return p2.count - p1.count;
            });
        },

        //初始化赛车数据
        initCarList: function(){
            let cars = [];
            this.recordsNotScreened.forEach((record,i)=>{
                let index = -1;
                let isExists = cars.some((car,j)=>{
                    if(record.car.id==car.car.id){
                        index = j;
                        return true;
                    }
                })
                if(!isExists){
                    cars.push({
                        car: record.car,
                        count: 1,
                    })
                }else{
                    cars[index].count++;
                }
            })
            this.carList = cars.sort(function compareFunction(p1, p2) {
                return p2.count - p1.count;
            });
        },
        //初始化宠物数据
        initPetList: function(){
            let pets = [];
            this.recordsNotScreened.forEach((record,i)=>{
                let index = -1;
                let isExists = pets.some((pet,j)=>{
                    if(record.pet.id==pet.pet.id){
                        index = j;
                        return true;
                    }
                })
                if(!isExists){
                    pets.push({
                        pet: record.pet,
                        count: 1,
                    })
                }else{
                    pets[index].count++;
                }
            })
            this.petList = pets.sort(function compareFunction(p1, p2) {
                return p2.count - p1.count;
            });
        },
        //初始化宠物数据
        initEcuList: function(){
            let ecus = [];
            this.recordsNotScreened.forEach((record,i)=>{
                let index = -1;
                let isExists = ecus.some((ecu,j)=>{
                    if(record.car.ecu.id==ecu.ecu.id){
                        index = j;
                        return true;
                    }
                })
                if(!isExists){
                    ecus.push({
                        ecu: record.car.ecu,
                        count: 1,
                    })
                }else{
                    ecus[index].count++;
                }
            })
            this.ecuList = ecus.sort(function compareFunction(p1, p2) {
                return p2.count - p1.count;
            });
        },
    }
}
</script>

<style lang="less" scope>
#list_statistics {
    padding: 0.5rem;

    &>* {
        margin-bottom: 1rem;
    }
}
</style>
