<template>
<div id="list_default">
    
    <el-drawer
        :visible.sync="trackDrawer"
        :direction="'btt'"
        :with-header="false">
        <div class="drawer_title">
            <span class="icon iconfont iconx1" @click="trackDrawer = false"></span>
            <span class="title">选择赛道分类</span>
            <span class="blank"></span>
        </div>
        <div class="drawer_body">
            <el-row class="row" :gutter="10" >
                <el-col class="col" :xs="24" :sm="6" :md="4" :lg="2" :xl="1"
                    v-for="r in recordsDefault" 
                    :key="r.id">
                    <div class="label" v-text="r.label" @click="trackDrawer = false;scrollTo(r.id)"></div>
                </el-col>
            </el-row>
        </div>
    </el-drawer>

    <div id="nav">
      <div class="nav_fix" :class="fixed?'fixed':''">
        <span v-show="!isNew" class="select" @click="trackDrawer = true"><span>导航</span><span class="icon iconfont iconxiala1"></span></span>
        <span class="blank"></span>
        <span class="order">
          <span class="default" :class="isNew?'':'active'" @click="isNew=false">默认</span>
          <span class="new" :class="isNew?'active':''" @click="isNew=true">最新</span>
        </span>
      </div>
    </div>
    <div class="content">
        <el-backtop :bottom="80" :right="20" style="opacity: 1 !important;"></el-backtop>

        <span class="playable"></span>
        <div v-if="!isNew" class="default">
            <APetEcuRecordList
                v-for="rs in recordsDefault"
                :key="rs.label"
                :records="rs.records"
                :trackLabel="rs.label"
                :id="rs.id"
                :typeNew="false">
            </APetEcuRecordList>
        </div>
        <div v-else class="new">
            <APetEcuRecordList
                v-for="rs in recordsNew"
                :key="rs.label"
                :records="rs.records"
                :trackLabel="rs.label"
                :typeNew="true">
            </APetEcuRecordList>
        </div>
    </div>
</div>
</template>

<script>
import APetEcuRecordList from "@/components/APetEcuRecordList";

export default {
    name: "ListDefault",
    components: {
        APetEcuRecordList
    },
    mounted(){
        console.log('传递的数据'+this.recordsNotScreened);
        window.addEventListener('scroll', this.fixedNav);
        this.initRecords();
    },
    destroyed(){
        window.removeEventListener('scroll', this.fixedNav);
    },
    computed: {

    },
    data() {
        
        return {
            isNew: false,
            fixed:false,
            recordsNotScreened:[],
            recordsDefault: [],
            recordsNew: [],
            trackDrawer: false,
        };
    },
    methods: {

        //初始化数据
        initRecords: function(){
            let that = this;
            this.$axios({
                method:'get',
                url:'./a_pet_ecu_records.json'
            }).then((response) =>{
                console.log(response)       //请求成功返回的数据
                that.recordsNotScreened = response.data;
                that.initRecordsDefault();
                that.initRecordsNew();
            }).catch((error) => {
                console.log(error)       //请求失败返回的数据
            });
        },

        //初始化默认数据
        initRecordsDefault: function(){
            let news = {label:'新图',id:'news',records:[]};
            let s7s = {label:'7星图',id:'s7s',records:[]};
            let s6s = {label:'6星图',id:'s6s',records:[]};
            let s5s = {label:'5星图',id:'s5s',records:[]};
            let s4s = {label:'4星图',id:'s4s',records:[]};
            let s3s = {label:'3星图',id:'s3s',records:[]};
            let s2s = {label:'2星图',id:'s2s',records:[]};
            let s1s = {label:'1星图',id:'s1s',records:[]};
            let s99s = {label:'拉力图',id:'s99s',records:[]};
            let seas = {label:'航海图',id:'seas',records:[]};
            let olds = {label:'怀旧图',id:'olds',records:[]};

            for (var r of this.recordsNotScreened) {
                if(r.track.new) {
                    news.records.push(r);
                }else if(r.track.old) {
                    olds.records.push(r);
                }else if(r.track.sea) {
                    seas.records.push(r);
                }else {
                    switch(r.track.stars) {
                        case 4: s4s.records.push(r);break;
                        case 3: s3s.records.push(r);break;
                        case 2: s2s.records.push(r);break;
                        case 1: s1s.records.push(r);break;
                        case 6: s6s.records.push(r);break;
                        case 5: s5s.records.push(r);break;
                        case 99: s99s.records.push(r);break;
                        case 7: s7s.records.push(r);break;
                        default: break;
                    }
                }
                //console.log(r);
            }

            if(news.records.length > 0) this.recordsDefault.push(news);
            if(s7s.records.length > 0) this.recordsDefault.push(s7s);
            if(s6s.records.length > 0) this.recordsDefault.push(s6s);
            if(s5s.records.length > 0) this.recordsDefault.push(s5s);
            if(s4s.records.length > 0) this.recordsDefault.push(s4s);
            if(s3s.records.length > 0) this.recordsDefault.push(s3s);
            if(s2s.records.length > 0) this.recordsDefault.push(s2s);
            if(s1s.records.length > 0) this.recordsDefault.push(s1s);
            if(s99s.records.length > 0) this.recordsDefault.push(s99s);
            if(seas.records.length > 0) this.recordsDefault.push(seas);
            if(olds.records.length > 0) this.recordsDefault.push(olds);
        },

        //初始化最新数据
        initRecordsNew: function(){
            let newArr = [];
            this.recordsNotScreened.forEach((item,i)=>{
                let index = -1;
                let isExists = newArr.some((newItem,j)=>{
                    if(item.date==newItem.label){
                        index = j;
                        return true;
                    }
                })
                if(!isExists){
                    newArr.push({
                        label: item.date,
                        records:[item]
                    })
                }else{
                    newArr[index].records.push(item);
                }
            })
            let that = this;
            this.recordsNew = newArr.sort(function compareFunction(r1, r2) {
                return that.getDate(r2.label).getTime() - that.getDate(r1.label).getTime();
            });

        },
 
        //字符串获取日期对象
        getDate: function(strDate) { 
          var st = strDate; 
          var s = st.split("-"); 
          var date = new Date(s[0], s[1], s[2]);
          return date; 
        },

        //固定导航
        fixedNav: function(){
            var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            var offsetTop = document.querySelector('#nav').offsetTop;
            this.fixed = (offsetTop - scrollTop <= 0);
        },

        //滑动到指定id
        scrollTo: function(el){
            document.querySelector("#"+el).scrollIntoView({
                behavior: "smooth", // 平滑过渡
                block: "start" // 上边框与视窗顶部平齐
            })
        }

    }
};
</script>

